import React, { useState } from 'react';
import './about.css';
import itb from '../assets/Logo ITB.png';
import itbm from '../assets/ITBM.png';

function About() {
  const [expandido, setExpandido] = useState(false);

  const handleLeiaMais = () => {
    setExpandido(!expandido); // Alterna entre expandido e recolhido
  };

  return (
    <div id='sobre-mim' className={`about-container ${expandido ? 'expandido' : 'recolhido'}`}> 
    <img src={itb} alt="Logo ITB" className="logo-itb" />
    <img src={itbm} alt="Logo ITB" className="logo-itbm" />
      <div className="about-content">
        <h2>Sobre mim</h2>
          <p>
          Atualmente, estou imerso no mundo do desenvolvimento de software, 
          buscando constantemente aprimorar minhas habilidades e conhecimentos em tecnologias inovadoras. 
          Meu foco  aplicar as melhores prticas de desenvolvimento para criar solues eficientes e funcionais, sempre com um olhar voltado para a experincia do usurio e a otimizao de processos.
          </p>
        {expandido && (
          <div className="extra-content">
            <p>
              Minha carreira teve incio como eletricista instalador, onde desenvolvi habilidades tcnicas robustas e um forte compromisso com a segurana e a eficincia. 
              Essa vivncia no setor eltrico me proporcionou uma base slida que me preparou para uma transio bem-sucedida para o desenvolvimento de software. 
              Aprendi a resolver problemas de maneira eficaz e a manter altos padres de qualidade, que se mostraram essenciais em minha nova trajetria.
            </p>
            <p>
              Estou sempre em busca de novas oportunidades de aprendizado e crescimento.
               Meu objetivo  aplicar minhas habilidades em um ambiente profissional dinmico, 
              contribuindo para projetos inovadores e colaborando com equipes dedicadas.
            </p>
            <p>
              Entusiasmado para transformar ideias em realidade, estou comprometido em agregar valor a cada projeto em que participo, 
              unindo minha experincia tcnica e minha paixo por tecnologia para impactar positivamente o mundo ao meu redor.
            </p>
          </div>
        )}
        <button className="leia-mais-btn" onClick={handleLeiaMais}>
          {expandido ? 'MOSTRE MENOS' : 'LEIA MAIS'}
        </button>
      </div>
    </div>
  );
}

export default About;
